<template>
    <div class="home">
        <div class="home__inputs">
          <div class="input-group mb-3">
            <span class="input-group-text" id="inputGroup-sizing-default">Oracle Label</span>
            <input v-model="oracleLabel"  placeholder="Oracle Label" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
          </div>
          <button @click="handleSetLabel" type="button" class="btn btn-primary">Set Label</button>
        </div>
    </div>
  </template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex'
  
  export default {
    data() {
          return {
            oracleLabel: ''
          }
      },
    name: 'OracleLabel',
    computed: {
      ...mapGetters(['IS_CONNECTED','LOADER'])
    },
    methods: {
      ...mapActions(['CONNECT_METAMASK','SEND_DATA']),
      handleSetLabel() {
        if (!this.oracleLabel) {
          this.$vToastify.info('Fill input', 'Validation warning')
        } else {
          const obj =  {
            method: 'setOracleLabel',
            params: [this.oracleLabel]
          }
          this.SEND_DATA(obj)
        }
      }
    },
  }
  </script>
  
  <style scoped>
  .home__inputs {
    width: 900px;
    margin: 0 auto;
  }
  </style>
  