<template>
  <div class="image-container set-full-height" style="background-color:burlywood">
    <div class="container">
      <div class="row">
        <div class="col-sm-8 col-sm-offset-2">
          <div class="wizard-container">
            <div class="card wizard-card" data-color="azure" id="wizard">
              <form action="" method="">
                <div class="wizard-header">
                  <h3 class="wizard-title">Become an oracle in three simple steps</h3>
                  <p class="category">Specify the labels and pass the vote</p>
                </div>
                <div class="wizard-navigation">
                  <div class="progress-with-circle">
                    <div class="progress-bar" role="progressbar" aria-valuenow="1" aria-valuemin="1" aria-valuemax="3" :style="progressByPercent"></div>
                  </div>
                  <ul class="nav nav-pills">
                    <li class="active" style="width: 33.3333%;">
                      <a @click="setStep(1)">
                        <div class="icon-circle">
                          <i class="ti-id-badge"></i>
                        </div>
                        Oracle Label
                      </a>
                    </li>
                    <li v-bind:class="{ active: step > 1 }" style="width: 33.3333%;">
                      <a @click="setStep(2)">
                        <div class="icon-circle">
                          <i class="ti-eye"></i>
                        </div>
                        Voting
                      </a>
                    </li>
                    <li v-bind:class="{ active: step > 2 }" style="width: 33.3333%;">
                      <a @click="setStep(3)">
                        <div class="icon-circle">
                          <i class="ti-list"></i>
                        </div>
                        Metric Labels
                      </a>
                    </li>
                  </ul>
                </div>
                <div class="tab-content">
                    <div class="tab-pane" v-bind:class="{ active: step === 1 }" id="details">
                      <div class="row">
                          <div class="col-sm-12">
                              <h5 class="info-text">The unique oracle label will allow users to identify you among other oracles</h5>
                          </div>
                          <div class="col-sm-10 col-sm-offset-1">
                              <div class="form-group">
                                  <label>Enter the oracle label and click "Set Oracle Label"</label>
                                  <input v-model="oracleLabel" type="text" class="form-control" id="exampleInputEmail1" placeholder="e.g Silicon Valley">
                              </div>
                          </div>
                          <div v-if="currentOracleLabel" class="col-sm-10 col-sm-offset-1">
                            <h5 class="info-text">The current label of your oracle is "{{currentOracleLabel}}", you can install a new label. If you are satisfied with the current one, go to the "Voiting" tab</h5>
                          </div>
                      </div>
                    </div>
                    <div class="tab-pane" v-bind:class="{ active: step === 2 }">
                        <div class="row">
                          <div class="col-sm-10 col-sm-offset-1">
                              <h5 class="info-text">Specify the metrics and data types that you will update, then wait for the successful completion of the vote. If the vote was successful, go to the "Metric Labels" tab</h5>
                          </div>
                          <div  v-for="metric, index in metrics" :key="index">
                            <div class="col-sm-5 col-sm-offset-1">
                              <div class="form-group">
                                  <label v-if="index === 0">Name of Metric</label>
                                  <input @change="onChangeName($event, index)" type="text" :value=metrics[index].name class="form-control" id="exampleInputEmail1" placeholder="Metric Name">
                              </div>
                            </div>
                            <div class="col-sm-5">
                              <div class="form-group">
                                <label v-if="index === 0">Type of Metric</label>
                                <select @change="onChangeType($event, index)" class="form-control">
                                  <option value="1">Uint</option>
                                  <option value="2">Int</option>
                                  <option value="3">FloatUint</option>
                                  <option value="4">FloatInt</option>
                                  <option value="5">Address</option>
                                  <option value="6">Bool</option>
                                  <option value="7">String</option>
                                  <option value="8">Date</option>
                                  <option value="9">Bytes32</option>
                                  <option value="10">KYC</option>
                                </select>
                              </div>
                            </div>
                          </div>
                          <button @click="handleAddMetric()" type="button" class="btn btn-next btn-fill btn-primary btn-wd"><i class="ti-plus"></i> Add Metric</button>
                          <div class="col-sm-10 col-sm-offset-1">
                            <div class="form-group">
                              <label>Description</label>
                              <textarea v-model='description' class="form-control" placeholder="" rows="2"></textarea>
                            </div>
                          </div>
                      </div>
                    </div>
                    <div class="tab-pane" v-bind:class="{ active: step === 3 }" id="description">
                      <div class="row">
                        <h5 v-if="currentNamesAndLabels.length" class="info-text col-sm-10 col-sm-offset-1">Metrics that you can change and their labels, you can set a label or change it to a new one</h5>
                        <div v-for="obj, index in currentNamesAndLabels" :key="index">
                          <div class="col-sm-5 col-sm-offset-1">
                            <div class="form-group">
                                <label v-if="index === 0">Name of Metrics</label>
                                <div type="text" class="form-control">{{obj.name}}</div>
                            </div>
                          </div>
                          <div class="col-sm-5">
                            <div class="form-group">
                                <label v-if="index === 0">Name of Labels</label>
                                <div type="text" class="form-control">{{obj.label}}</div>
                            </div>
                          </div>
                        </div>
                        <h5 v-if="currentNamesAndLabels.length" class="info-text col-sm-10 col-sm-offset-1">Labels customizing:</h5>
                        <h5 v-else class="info-text col-sm-10 col-sm-offset-1">Wait until the community approves your metrics</h5>
                        <div v-if="currentNamesAndLabels.length" v-for="metric, index in namesAndLabels" :key="index + 'a'">
                          <div class="col-sm-5 col-sm-offset-1">
                            <div class="form-group">
                                <label v-if="index === 0">Name of Metric</label>
                                <input @change="onChangeNameForSet($event, index)" type="text" :value=namesAndLabels[index].name class="form-control" id="exampleInputEmail1" placeholder="Metric Name">
                            </div>
                          </div>
                          <div class="col-sm-5">
                            <div class="form-group">
                              <label v-if="index === 0">Label of Metric</label>
                              <input @change="onChangeLabelForSet($event, index)" type="text" :value=namesAndLabels[index].label class="form-control" id="exampleInputEmail1" placeholder="Metric Label">
                            </div>
                          </div>
                        </div>
                        <button v-if="currentNamesAndLabels.length" @click="handleAddLabelForSet()" type="button" class="btn btn-next btn-fill btn-primary btn-wd"><i class="ti-plus"></i> Add Metric</button>
                      </div>
                    </div>
                </div>
                <div class="wizard-footer">
                  <div class="pull-right">
                    <input v-if="step === 1" @click="handleSetLabel()" type='button' class='btn btn-next btn-fill btn-primary btn-wd' name='next' value='Set Oracle Label' />
                    <input v-if="step === 2" @click="handleBecomeOracle()" type='button' class='btn btn-next btn-fill btn-primary btn-wd' name='next' value='Become Oracle' />
                    <input v-if="step === 3" @click="handleSetMetricLabels()" type='button' class='btn btn-next btn-fill btn-primary btn-wd' name='next' value='Set Metric Labels' />
                  </div>
                  <div class="clearfix"></div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div> 

  <div class="footer">
    <div class="container text-center">
          Made with <i class="fa fa-heart heart"></i> by <a href="https://www.creative-tim.com">Creative Tim</a>. Free download <a href="https://www.creative-tim.com/product/paper-bootstrap-wizard">here.</a>
    </div>
    <div class="container text-center">
      Copyright (c) 2017 Creative Tim
    </div>
  </div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Oracle from "../components/Oracle.vue";
import {
  getFirstAddress,
  fetchContractData
} from '../utils/web3.js';
import { 
  // methodsByType,
  // converter,
  getHash
} from '@/utils/metricTypes.js'

export default {
  data() {
        return {
          step: 1,
          currentOracleLabel: '',
          metrics: [
            {
              name: '',
              type: '1'
            }
          ],
          namesAndLabels: [
            {
              name: '',
              label: ''
            }
          ],
          currentNamesAndLabels: [],
          oracleLabel: '',
          description: '',
        }
    },
  name: 'Main',
  async created() {
    const currentAddress = await getFirstAddress();
    const oracleinfo = await fetchContractData('getOracleInfo', [currentAddress]);
    console.log('oracleinfo',oracleinfo);
    this.currentOracleLabel = oracleinfo[1];
    if(oracleinfo[0].length) {
      for(let i = 0; i < oracleinfo[0].length; i++) {
        const hash = getHash(oracleinfo[0][i]);
        const metricinfo = await fetchContractData('getMetricaInfo', [hash])
        this.currentNamesAndLabels.push({name: oracleinfo[0][i], label: metricinfo.label})
      }
    }
  },
  components: {
    Oracle
  },
  computed: {
    ...mapGetters(['IS_CONNECTED','LOADER','ORACLE_PROPOSALS']),
    progressByPercent() {
      switch (this.step) {
        case 1:
          return 'width: 21%;'
        case 2:
          return 'width: 50%;'
        case 3:
          return 'width: 79%;'
      }
    }
  },
  methods: {
    ...mapActions(['CONNECT_METAMASK','SEND_DATA']),
    handleAddMetric() {
      this.metrics.push({
              name: '',
              type: '1'
            })
    },
    handleAddLabelForSet() {
      this.namesAndLabels.push({
              name: '',
              label: ''
            });
    },
    setStep(step) {
      this.step = step;
    },
    onChangeNameForSet(event, index) {
      this.namesAndLabels[index].name = event.target.value; 
    },
    onChangeLabelForSet(event, index) {
      this.namesAndLabels[index].label = event.target.value; 
    },
    onChangeType(event, index) {
      this.metrics[index].type = event.target.value; 
    },
    onChangeName(event, index) {
      this.metrics[index].name = event.target.value;
    },
    handleSetLabel() {
      if (!this.oracleLabel) {
        this.$vToastify.info('Enter oracle label', 'Validation warning')
      } else {
        const obj =  {
          method: 'setOracleLabel',
          params: [this.oracleLabel]
        }
        this.SEND_DATA(obj)
      }
    },
    handleBecomeOracle() {
      if(!this.description) {
        this.$vToastify.info('Fill description', 'Validation warning')
      } else {
        const names = [];
        const types = [];
        this.metrics.forEach(function callback(currentValue, index, array) {
          if(array[index].name.trim()) {
            names.push(currentValue.name.trim());
            types.push(currentValue.type);
          } 
        })
        if(names.length === 0) {
          this.$vToastify.info('Enter at least one name', 'Validation warning')
        } else {
          const obj =  {
            method: 'becomeOracle',
            params: [names, types, this.description]
          }
          this.SEND_DATA(obj)
        }
      }
    },
    handleSetMetricLabels() {
      const names = [];
      const labels = [];
      this.namesAndLabels.forEach(function callback(currentValue, index, array) {
        if(array[index].name.trim() && array[index].label.trim()) {
          names.push(currentValue.name.trim());
          labels.push(currentValue.label.trim());
        } 
      })
      if(names.length === 0) {
          this.$vToastify.info('Enter at least one name and label', 'Validation warning')
      } else {
        const obj =  {
          method: 'setMetricLabels',
          params: [names, labels]
        }
        this.SEND_DATA(obj)
      }
    }
  },
}
</script>
