<template>
    <div class="card" style="width: 18rem;">
        <div class="card-body">
          <p class="card-title">{{proposal.member}}</p>
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">{{endTime}}</li>
          <li class="list-group-item">{{proposal.support}}</li>
        </ul>
        <div class="card-body">
          <button @click="handleVoteMemberProposal(proposal.id)" v-if="isVoteTime" type="button" class="btn btn-secondary">Vote</button>
          <button @click="handleFinishMemberProposal(proposal.id)" v-else type="button" class="btn btn-secondary">Finish</button>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    created() {
        this.date = this.proposal.endTime
    },
    data() {
        return {
            date: '',
        }
    },
    props: {
        proposal: Object,
    },
    computed: {
        endTime() {
            return new Date(this.date * 1000); 
        },
        isVoteTime() {
            return Date.now() <= this.date * 1000
        }
    },
    methods: {
        ...mapActions(['SEND_DATA']),
        handleVoteMemberProposal(id) {
            const obj = {
                method: 'voteForMember',
                params: [id]
            }
            this.SEND_DATA(obj)
        },
        handleFinishMemberProposal(id) {
            const obj = {
                method: 'finishMemberVoiting',
                params: [id]
            }
            this.SEND_DATA(obj)
        }
    }
}
</script>