<template>
    <div class="home">
      <div class="home__inputs">
        <button @click="handleBecomeMember" type="button" class="btn btn-primary">Become Part of Community</button>
      </div>
      <button @click="GET_MEMBER_PROPOSALS" type="button" style="margin-top: 20px;" class="btn btn-outline-warning">Update cards</button>
      <div class="home__cards">
          <Member :proposal="proposal" v-for="proposal, index in MEMBER_PROPOSALS" :key="index" />
      </div>
    </div>    
</template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex';
  import Member from "../components/Member";

  
  export default {
    async created() {
        await this.GET_MEMBER_PROPOSALS();
    },
    components: {
      Member
    },
    computed: {
    ...mapGetters(['IS_CONNECTED','LOADER', 'MEMBER_PROPOSALS'])
    },
    methods: {
    ...mapActions(['CONNECT_METAMASK', 'GET_MEMBER_PROPOSALS', 'SEND_DATA']),
    handleBecomeMember() {
      const obj =  {
        method: 'becomeMember',
        params: []
      }
      this.SEND_DATA(obj)
    }
    }
  }
  </script>

<style scoped>
.home__inputs {
  width: 900px;
  margin: 0 auto;
}
.home__cards {
  width: 900px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.card {
  margin-bottom: 30px;
}
</style>