<template>
    <div class="card" style="width: 18rem;" v-bind:class="{ kyc: proposal.type == 1 }">
        <div class="card-body">
          <p class="card-title">{{proposal.oracle}}</p>
          <p class="card-text">{{proposal.description}}</p>
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">{{proposal.names}}</li>
          <li class="list-group-item">{{proposal.metricTypes}}</li>
          <li class="list-group-item">{{endTime}}</li>
          <li class="list-group-item">{{proposal.support}}</li>
        </ul>
        <div class="card-body">
            <button @click="handleVoteForOracle(proposal.id)" v-if="isVoteTime" type="button" class="btn btn-secondary">Vote</button>
            <button @click="handleFinishOracleVoiting(proposal.id)" v-else type="button" class="btn btn-secondary">Finish</button>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    created() {
        this.date = this.proposal.endTime
    },
    data() {
        return {
            date: '',
        }
    },
    props: {
        proposal: Object,
    },
    computed: {
        endTime() {
            return new Date(this.date * 1000); 
        },
        isVoteTime() {
            return Date.now() <= this.date * 1000
        }
    },
    methods: {
    ...mapActions(['SEND_DATA']),
    handleVoteForOracle(id) {
        const obj = {
            method: 'voteForOracle',
            params: [id]
        }
        this.SEND_DATA(obj)
    },
    handleFinishOracleVoiting(id) {
        const obj = {
            method: 'finishOracleVoiting',
            params: [id]
        }
        this.SEND_DATA(obj)
    }
    }
}
</script>

<style scoped>
.kyc {
    background-color: #ced4da;
}
</style>