<template>
    <div class="home">
        <div class="home__inputs">
          <div class="input-group mb-3">
            <span class="input-group-text" id="inputGroup-sizing-default">Metric Name</span>
            <input v-model="metricName"  placeholder="metric1, metric2, metric3..." type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
          </div>
          <div class="input-group mb-3">
            <span class="input-group-text" id="inputGroup-sizing-default">Metric Label</span>
            <input v-model="metricLabel"  placeholder="label1, label2, label3..." type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
          </div>
          <button @click="handleSetLabels" type="button" class="btn btn-primary">Set Labels</button>
        </div>
    </div>
  </template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex'
  
  export default {
    data() {
          return {
            metricName: '',
            metricLabel: '',
          }
      },
    name: 'MetricLabels',
    computed: {
      ...mapGetters(['IS_CONNECTED','LOADER'])
    },
    methods: {
      ...mapActions(['CONNECT_METAMASK','GET_ORACLE_PROPOSALS','SEND_DATA']),
      handleSetLabels() {
        if (!this.metricName) {
          this.$vToastify.info('Enter the names of the metrics separated by commas', 'Validation warning')
        } else {
          let metricsArray = this.metricName.split(",");
          let labelArray = this.metricLabel.split(",");
          metricsArray.forEach(function callback(currentValue, index, array) {
            array[index] = currentValue.trim();
          })
          labelArray.forEach(function callback(currentValue, index, array) {
            array[index] = currentValue.trim();
          })
          const names = metricsArray.filter(word => word);
          const labels = labelArray.filter(type => type);
          if(names.length != labels.length) {
            this.$vToastify.info('Arrays must be the same length', 'Validation warning');
          } else {
            const obj =  {
              method: 'setMetricLabels',
              params: [names,labels]
            }
            this.SEND_DATA(obj)
          }
        }
      }
    },
  }
  </script>
  
  <style scoped>
  .home__inputs {
    width: 900px;
    margin: 0 auto;
  }
  </style>
  